html, body {
  box-sizing: border-box;
  color: #060607;
  font: normal 125%/1.6 "Didact Gothic", serif;
  height: 100%;
  padding: 0;
}

*, *::before, *::after {
  box-sizing: inherit;
}

a:link, a:visited {
  color: #5d2189;
}
a:active, a:focus, a:hover {
  color: #8c6c0c;
}

body {
  font-size: 1.125rem;
  position: relative;
}

nav .menu {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

header.banner {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 128px 1fr;
  grid-template-areas: "logo page-title" "logo menu";
}
header.banner .logo {
  grid-area: logo;
}
header.banner #page-title {
  align-self: end;
  font-weight: bold;
  font-family: "Poiret One", sans-serif;
  font-size: 1.75rem;
  grid-area: page-title;
  line-height: 1;
  margin: 0;
}
header.banner .menu {
  grid-area: menu;
}
header.banner .menu a {
  margin: 0 1rem;
  text-decoration: none;
}
header.banner .menu li {
  border-right: 1px solid #060607;
}
header.banner .menu li:first-child a {
  margin-left: 0;
}
header.banner .menu li:last-child {
  border-right-style: none;
}

/*!
 * WP Core CSS
 * ===========
 * Use with _mixins.scss imported prior to this file
 */
.alignnone {
  margin: 0 1.25rem 1.25rem 0;
}

.aligncenter {
  clear: both;
  display: block;
  margin: 0.3125rem auto;
}

.alignright {
  float: right;
  margin: 0.3125rem 0 1.25rem 1.25rem;
}

.alignleft {
  float: left;
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

a img.alignright {
  float: right;
  margin: 0.3125rem 0 1.25rem 20px;
}

a img.alignnone {
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

a img.alignleft {
  float: left;
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%; /* Image does not overflow the content area */
  padding: 0.3125rem 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

.wp-caption.alignleft {
  margin: 0.3125rem 1.25rem 1.25rem 0;
}

.wp-caption.alignright {
  margin: 0.3125rem 0 1.25rem 1.25rem;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 0.6875rem;
  line-height: 1.0625rem;
  margin: 0;
  padding: 0 0.25rem 0.3125rem;
}